export default BlogsScreen;

export const query = graphql`
  query MyQuery {
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
    allDataJson {
      edges {
        node {
          id
          keyId
          slug
          author_name
          blog_created_date
          title
          short_description
          description
          meta_title
          meta_description
          image
          category
          tags
        }
      }
      pageInfo {
        perPage
        hasNextPage
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
        siteUrl
      }
    }
    allFile {
      nodes {
        relativePath
      }
    }
    allMetaJson(filter: { slug: { eq: "blog" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
